<template>
	<div>
		<vc-page-header></vc-page-header>
  	<vc-menus :menus="menus"></vc-menus>

	  <div class="root-content">
			<router-view ></router-view>
	  </div>
	</div>
</template>

<script type="text/babel">
  export default {
		data() {
			return {
				menus: []
			}
		},

    created() {
			this.$notify({
        title: '提示',
				type: 'success',
				offset: 64,
				duration: 3000,
        message: '欢迎来到webutf.com'
      })
			this.$root.token = localStorage.getItem('pwd') || ''
	    this.pathChange(this.$route.path)
    },

		methods: {
	    pathChange(path) {
	      if (path.indexOf('component') > -1) {
	        this.menus = this.$config.use
	      } else if (path.indexOf('demo') > -1 || path.indexOf('fullpage') > -1) {
	        this.menus = this.$config.demo
	      } else {
					this.menus = this.$config.basic
				}
	    }
	  },

	  watch: {
	    '$route.path'(path) {
	      this.pathChange(path)
	    }
	  }
	}

</script>

<style scoped lang="scss">
	.root-content {
		margin-top: 70px;
		padding: 40px 40px 100px 40px;
		min-height: 800px;
		background: #fff;
		box-sizing: border-box;
		position: relative;
	}
</style>
